import { request } from 'utils';

const prefix = '/api/finance';

export const getRevenueAndExpeditureAmountInCurrentYear = ({ query } = {}) =>
  request({
    prefix,
    url: '/QuickLink/GetRevenueAndExpeditureAmountInCurrentYear',
    method: 'GET',
    params: { query },
    data: {},
  });

export const getReceivableNo = ({ query } = {}) =>
  request({
    prefix,
    url: '/QuickLink/GetReceivableNo',
    method: 'GET',
    params: { query },
    data: {},
  });

export const getFeeObjectNo = ({ query } = {}) =>
  request({
    prefix,
    url: '/QuickLink/GetFeeObjectNo',
    method: 'GET',
    params: { query },
    data: {},
  });

export const getReceiptConfigNo = ({ query } = {}) =>
  request({
    prefix,
    url: '/QuickLink/GetReceiptConfigNo',
    method: 'GET',
    params: { query },
    data: {},
  });


