import React from 'react';
import { Spin, Icon } from 'antd';
import { Link } from 'react-router-dom';
import './style.scss';

const LoadingIcon = <Icon type="loading" style={{ fontSize: 16, color: '#fff' }} spin />;

export default class InfoCountTotalCard extends React.Component {
  static defaultProps = {
    title: 'Card Title',
    icon: 'bullhorn',
    type: '',
    btnType: 'default',
    total: '',
    backgroundColor: '#0088FE',
    color: '#fff',
    url: '/',
    service: false,
    loading: false,
  };

  state = {
    totalService: 0,
  };

  componentDidMount = () => {
    this.getTotalByService();
  };

  getTotalByService = async () => {
    const { service } = this.props;
    if (!service) {
      return;
    }
    this.setState({ loading: true });
    const res = await service();
    if(res.data && 'number' in res.data) {
      this.setState({ totalService: res.data.number, loading: false });
    } else {
      this.setState({ 
        totalService: res.data.columns[0].value, 
        moreInfo: {
          label: res.data.columns[1].label,
          value: res.data.columns[1].value,
        },
        loading: false,
      });
    }
  };

  renderIcon = () => {
    const { icon } = this.props;
    if (!icon) {
      return null;
    }
    if (typeof icon === 'string') {
      return (
        <span className="infoCard__digit">
          <i className={`icmn-${icon}`} />
        </span>
      );
    }
    return <span className="infoCard__digit">{icon({ width: 40, color: '#fff' })}</span>;
  };

  render() {
    const { type, title, backgroundColor, color, url } = this.props;
    const { totalService, moreInfo, loading } = this.state;
    const className = `infoCard ${type.length > 0 ? `infoCard--${type}` : ''}`;
    return (
      <div>
        <Link to={url}>
          <span style={{ backgroundColor, color }} className={className}>
            {this.renderIcon()}
            <span className="infoCard__desc">
              <span style={{ color }} className="infoCard__title infoCart_link">
                {title}
              </span>
              <p>Tổng: {loading ? <Spin indicator={LoadingIcon} /> : totalService} {moreInfo && `(${moreInfo.value} ${moreInfo.label}) `}</p>
            </span>
          </span>
        </Link>
      </div>
    );
  }
}



