import React, { Component } from 'react';
import Page from 'components/LayoutComponents/Page';
import { Row, Col } from 'antd';
import InfoCountTotalCard from 'components/common/InfoCard/index';
import {
  getReceivableNo,
  getFeeObjectNo,
  getReceiptConfigNo,
} from 'services/financeQuicklink';

const COLORS = [
  '#0088FE',
  '#00C49F',
  '#FFBB28',
  '#FF8042',
  '#0088fed9',
  '#00c49fd9',
  '#ffbb28d9',
  '#ff8042d9',
];

const data = [
  {
    title: 'Khoản thu',
    key: 'Danh mục khoản thu',
    url: '/accounts-receivable/list',
    icon: 'icmn icmn-coin-dollar',
    service: getReceivableNo,
  },
  {
    title: 'Đối tượng học phí',
    key: 'ĐT nộp học phí',
    url: '/fee-submit-subject-type/list',
    icon: 'icmn icmn-users',
    service: getFeeObjectNo,
  },
  {
    title: 'Loại hóa đơn',
    key: 'Quản lý hóa đơn',
    url: '/bill-management/list',
    icon: 'icmn icmn-file-text',
    service: getReceiptConfigNo,
  },
];

class FinanceCategoryPage extends Component {
  state = {};
  render() {
    return (
      <Row gutter={24}>
        {data.map((item, index) => (
          <Col span={6} key={item.title}>
            <InfoCountTotalCard
              backgroundColor={COLORS[index % 8]}
              title={item.title}
              service={item.service}
              icon={item.icon}
              url={item.url}
            />
          </Col>
        ))}
      </Row>
    );
  }
}

export default props => (
  <Page {...props}>
    <FinanceCategoryPage {...props} />
  </Page>
);



